import React, { Component } from 'react';
import Typist from 'react-typist';
import TypistLoop from 'react-typist-loop'
export default class Header extends Component {
  render() {
    let cvData = this.props.cvData;
    return (
      <React.Fragment>
      <header id="home">
         <nav id="nav-wrap">
            <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
          <a className="mobile-btn" href="#" title="Hide navigation">Hide navigation</a>
            <ul id="nav" className="nav">
               <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
               <li><a className="smoothscroll" href="#about">About</a></li>
               <li><a className="smoothscroll" href="#resume">Resume</a></li>
               <li><a className="smoothscroll" href="#portfolio">Works</a></li>
               <li><a className="smoothscroll" href="#blogs">Blogs</a></li>
               <li><a className="smoothscroll" href="#contact">Contact</a></li>
            </ul>
         </nav>

         <div className="row banner">
            <div className="banner-text">              
                {/* <h1 className="responsive-headline">I am </h1> */}
                <h1 className="responsive-headline">{cvData.firstName} <span>{cvData.lastName} </span></h1> 
                <h2>I am a <TypistLoop interval={300}>{cvData.roles.map(text => <Typist key={text} startDelay={1000}> <span> {text}</span></Typist>)}</TypistLoop></h2>
                <h3>{cvData.roleDescription}</h3>
               {/* <hr/> */}
               <ul className="social">
                  {
                    cvData.socialLinks && cvData.socialLinks.map(item =>{
                      return(
                              <li key={item.name}>
                                <a href={item.url} target="_blank"><i className={item.className}></i></a>
                              </li>
                            )
                          }
                    )
                  }
               </ul>
            </div>
         </div>

         <p className="scrolldown">
            <a className="smoothscroll" href="#about"><i className="icon-down-circle"></i></a>
         </p>

      </header>
      </React.Fragment>
    );
  }
}