import React, { Component } from 'react';
export default class About extends Component {
  render() {
    let cvData = this.props.cvData;
    return (
      <section id="about">
         <div className="row">

            <div className="three columns">

               <img className="profile-pic"  src="images/Satyam.jpg" alt="" />
               <div data-iframe-width="150" data-iframe-height="270" data-share-badge-id="824be7bc-a162-49e3-b5b8-1cb35a75adc5" data-share-badge-host="https://www.youracclaim.com"></div><script type="text/javascript" async src="//cdn.youracclaim.com/assets/utilities/embed.js"></script>
               <div data-iframe-width="150" data-iframe-height="270" data-share-badge-id="14c81310-4678-4a96-ab6d-e860105174db" data-share-badge-host="https://www.youracclaim.com"></div><script type="text/javascript" async src="//cdn.youracclaim.com/assets/utilities/embed.js"></script>
               {/* {cvData.certifications && cvData.certifications.map(item =>{
                  return(
                        <h3>{item.name}</h3>
                  )                  
               })} */}
            </div>

            <div className="nine columns main-col">

               <h2>Who am I?</h2>
               <div className="row">
               
                  <div className="columns contact-details">
                  <p className="aboutme">{cvData.aboutme}</p>
                  <p className="address">
                        <span>Name: {cvData.name}</span>
                        <br></br>
                        <span>Email me at: <a href={cvData.emailId} target="_blank" >{cvData.emailIdText}</a></span>
                        <br></br>
                        <span>Current Location: {cvData.address}</span>
                        <br></br>
                        <span>Find more at: <a href={cvData.website} target="_blank" >{cvData.website}</a></span>
       				</p>
                  </div>
               </div>
               

               
            </div>
         </div>
      </section>
    );
  }
}